<template>
  <div>
    <loading :active.sync="isLoading">
      <img alt="Meason" src="../../assets/img/loading.svg" width="100">
    </loading>
    <div class="user-title">
      <button class="btn btn-outline-secondary btn-sm" type="button" onclick="window.history.back();">
        <i class="fas fa-arrow-left"></i>
      </button>
      <h2><i class="fas fa-plus-circle"></i> 發布音樂作品</h2>
    </div>
    <div class="form">
      <form action="" @submit.prevent="">
        <div class="row">
          <div class="col-md-12">
            <div class="form-group">
              <label for="title"><i class="fas fa-music"></i>音樂名稱<b>*</b></label>
              <input type="text" class="form-control" placeholder="請輸入音樂名稱"
                id="title" name="title" data-vv-as="音樂名稱"
                :class="{'is-invalid': errors.has('title')}"
                v-model="formData.name" v-validate="'required|max:50'">
              <p class="text-danger mt-1 small" v-if="errors.has('title')">
                {{ errors.first('title') }}
              </p>
            </div>
          </div>
          <div class="col-md-6">
            <div class="form-group">
              <label for="type"><i class="fas fa-music"></i>選擇類型<b>*</b></label>
              <button type="button" class="form-control typeText" id="type"
                :class="{'is-invalid': tempDom.errorInput === 'type'}"
                data-toggle="modal" data-target="#type-modal">
                <span v-if="!formData.type[0]">選擇作品類型</span>
                <p class="texts">
                  <span v-for="(item, index) in formData.type" :key="index">{{item}}</span>
                </p>
              </button>
              <p class="text-danger mt-1 small"
                v-if="tempDom.errorInput === 'type'">未選擇樂手</p>
            </div>
          </div>
          <div class="col-md-6">
            <div class="form-group">
              <label for="photos"><i class="fas fa-music"></i>作品封面<b>*</b></label>
              <button class="form-control" id="photos"
                :class="{'is-invalid': tempDom.errorInput === 'photo'}"
                @click="tempDom.errorInput = null"
                data-toggle="modal" data-target="#upload-img">
                <article v-if="formData.cover">
                  <div :style="{backgroundImage: `url(${formData.cover})`}"></div> 作品封面
                </article>
                <span v-else>選擇圖片</span>
              </button>
              <p class="text-danger mt-1 small"
                v-if="tempDom.errorInput === 'photo'">未選擇圖片</p>
            </div>
          </div>
          <div class="col-md-12">
            <div class="form-group">
              <label for="bio"><i class="fas fa-music"></i>作品介紹 / 歌詞<b>*</b></label>
              <textarea rows="10" class="form-control" placeholder="可在此填寫創作理念、歌詞等作品相關介紹"
                name="bio" id="bio" data-vv-as="介紹"
                :class="{'is-invalid': errors.has('bio')}"
                v-model="formData.bio" v-validate="'required'"></textarea>
            </div>
            <p class="text-danger mt-1 small" v-if="errors.has('bio')">
              {{ errors.first('bio') }}
            </p>
          </div>
          <div class="col-md-12">
            <label for="song"><i class="fas fa-music"></i>音樂作品檔案<b>*</b></label>
            <div class="input-group" v-if="!tempDom.uploadDone">
              <input type="file" name="song" id="song"
                multiple="true" accept="audio/mp3"
                class="form-control"
                v-validate="'required|size:10000|ext:mp3'" data-vv-as="音樂檔案"
                @change="tempDom.errorInput = null"
                :disabled="tempDom.uploadText !== ''">
              <div class="input-group-append">
                <button class="btn btn-info " type="button"
                  @click="uploadSong()"
                  @focus="tempDom.errorInput = null"
                  :disabled="tempDom.uploadText !== '' || errors.has('song')">
                  <span v-if="tempDom.uploadText && tempDom.uploadText !== '上傳完成'">
                    <i class="fas fa-spinner fa-spin"></i>
                  </span>                  
                  上傳音樂
                </button>
              </div>
            </div>
            <span class="text-success ml-3" v-else>
              音樂檔案上傳完成
              <a href="#"
                @click.prevent="tempDom.uploadDone = false; formData.url = ''">(修改)</a>
            </span>
            <div class="progress" v-if="tempDom.uploadText && !tempDom.uploadDone">
              <div class="progress-bar progress-bar-striped progress-bar-animated"
                role="progressbar"
                aria-valuemin="0" aria-valuemax="100"
                id="progressing"></div>
            </div>
            <p class="text-danger mt-1 small"
              v-if="errors.has('song') || tempDom.errorInput === 'song'">
              請選擇小於 10MB 的 .mp3 音樂檔案
            </p>
            <p class="text-success mt-1 small"
              v-if="tempDom.uploadText && !tempDom.uploadDone">
              {{tempDom.uploadText}}
            </p>
          </div>
          <div class="col-lg-12 mt-5 mb-5">
            <button class="btn btn-primary form-control"
             @click="creatPost()" :disabled="!tempDom.uploadDone">立即發佈</button>
          </div>
        </div>
      </form>
    </div>
    <!-- Modal -->
    <div class="modal fade" id="type-modal" tabindex="-1" role="dialog" aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">
              選擇類型
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-md-12">
                <div class="input-group mb-3">
                  <input type="text" class="form-control" placeholder="♫ Search" 
                    aria-label="" aria-describedby="basic-addon1" v-model="tempDom.filterInput">
                </div>	
              </div>
            </div>
            <div class="row" id="instrumentItem">
              <div class="col-md-4 col-sm-4 col-6" 
                v-for="(item, index) in tempDom.filterItem" :key="index"
                v-show="item">
                <div class="radio">
                  <input type="checkbox" name="radio"
                    :disabled="formData.type.length >= 3 && !tempDom.selectType[index]"
                    v-model="tempDom.selectType[index]" :id="`radio${index}`" :value="item">
                  <label :for="`radio${index}`">
                    {{item}}
                  </label>
                </div>
              </div>
            </div>
            <p class="text-primary mt-1 small" v-if="formData.type.length >= 3">
              最多只能選三項
            </p>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-primary" data-dismiss="modal"
              @click="tempDom.errorInput = null">選擇完成</button>
          </div>
        </div>
      </div>
    </div>
    <!-- 上傳圖片 Modal -->
    <div class="modal fade" id="upload-img" tabindex="-1" role="dialog" aria-hidden="true"
      data-backdrop="false" data-keyboard="false">
      <div class="modal-dialog modal-dialog-centered modal-lg" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">
              選擇音樂封面
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <label for="file-input" class="files">
              <img src="../../assets/img/bg/images-select.svg" alt="音樂封面選擇" 
                v-if="!tempDom.imgurl || errors.has('photo')" class="select-img">
              <div class="img-preview" v-else>
                <img :src="tempDom.imgurl" id="scream"/>
              </div>                  
              <input type="file" name="photo" id="file-input"
                multiple="true" accept="image/*" @change="preview"
                v-validate="'size:5000|ext:jpg,png'" data-vv-as="圖片檔案">
              <p class="text-danger mt-1 small" v-if="errors.has('photo')">
                請選擇小於 5MB 的圖片
              </p>
            </label>  
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-primary"
              :disabled="errors.has('photo')"
              @click="uploadImg()">
              上傳圖片
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from 'firebase';
import $ from 'jquery';

export default {
  props: ['userData'],
  data() {
    return {
      isLoading: false,
      formData:{
        name: '',
        type: [],
        bio: '',
        cover: ''
      },
      tempDom: {
        errorInput: '',
        filterInput: '',
        filterItem: ['COVER', '電音', '搖滾', '民謠'],
        selectType: [],
        imgurl: '',
        uploadText: '',
        uploadDone: false,
        alldataDone: false,
      },
      tempData: {
        options: ['COVER', '電音', '搖滾', '民謠'],
      }
    }
  },
  watch: {
    'tempDom.filterInput': {
      handler: function filterItems(val) {
        const vm = this;
        vm.tempDom.filterItem = vm.tempData.options.map(function(item, index, array){
          if (item.indexOf(val) !== -1) {
            return item;
          }
           return false;
        });
      }
    },
    'tempDom.selectType': {
      handler: function selectType(val) {
        const vm = this;
        vm.formData.type = [];
        val.forEach(function(item, index){
          if(item) {
            vm.formData.type.push(vm.tempData.options[index])
          }
        });
      }
    },
    'formData.bio': {
      handler: function autogrow(val) {
        const textarea = document.getElementById('bio');
        let adjustedHeight = textarea.clientHeight;
        adjustedHeight = Math.max(textarea.scrollHeight,adjustedHeight);
        if (adjustedHeight > textarea.clientHeight) {
          textarea.style.height=adjustedHeight+'px';
        }
      },
    },
  },
  methods: {    
    preview(e) {
      const vm = this;
      const file = e.target.files[0];
      vm.tempDom.imgurl = URL.createObjectURL(file);
    },
    cvs() {
      const vm = this;
      let imgNewWidth = 600, // 圖片新寬度
      canvas = document.createElement("canvas"),
      img = new Image(),
      context = canvas.getContext("2d");
      
      let imgfile = $('#scream'),
      imgH = imgfile.height(),
      imgW = imgfile.width(),
      imgNewHeight = imgNewWidth * imgH / imgW, // 圖片新高度
      newImg

      img.src = vm.tempDom.imgurl;
      canvas.width = imgNewWidth;
      canvas.height = imgNewHeight;
      context.clearRect(0, 0, imgNewWidth, imgNewHeight);
      context.drawImage(img, 0, 0, imgNewWidth, imgNewHeight); // 調整圖片尺寸
      newImg = canvas.toDataURL("image/jpeg", 1);
      return newImg
    },
    uploadImg() {
      const vm = this;
      vm.isLoading = true;
      var timestamp = Number(new Date()); // 檔名設定
      let fileName = timestamp.toString() + vm.userData.uid;
      var storageRef = firebase.storage().ref('music_photo/' + fileName); //資料夾+黨名
      var file = vm.dataURLtoFile(vm.cvs(), 'fileName');
      storageRef.put(file)
      .then((snapshot) => {
        // 取得圖片位置
        snapshot.ref.getDownloadURL().then(function(downloadURL) {
          vm.formData.cover = downloadURL;
          $('#upload-img').modal('hide');
          vm.isLoading = false;
        });
      })
      .catch((error) => {
        alert('上傳失敗，請重新整理後重試');
        console.log(error)
      });
    },
    dataURLtoFile(dataurl, filename) {
      var arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
      while(n--){
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr], filename, {type:mime});
    },
    creatPost() {
      const vm = this;      
      this.$validator.validate().then((result) => {
        if (result) {
          // do stuff if not valid.          
          if (!vm.formData.type[0]) {
            vm.tempDom.errorInput = 'type';
          } else if(!vm.formData.cover) {
            vm.tempDom.errorInput = 'photo';
          } else if(!vm.formData.url) {
            vm.tempDom.errorInput = 'song';
          } else {
            // 確保有登入
            vm.tempDom.alldataDone = true;
            if(vm.userData){
              vm.sendPostAPI();
            } else {
              alert('會員登入狀況出現錯誤，請重新登入');
              vm.$router.push('/');
            }
          }
        } else {
          console.log('欄位不完整')
        }
      });
    },
    sendPostAPI() {
      const vm = this;
      vm.isLoading = true;
      const url = `${process.env.VUE_APP_API}/creatMusic`;
      const newformData = {
        artist: vm.userData.displayName,
        bio: vm.formData.bio,
        timestamp: Date.now(),
        legal: true,
        display: true,
        type: vm.formData.type,
        cover: vm.formData.cover,
        poster_uid: vm.userData.uid,
        name: vm.formData.name,
        url: vm.formData.url,
        view: 0,
        fileName: vm.formData.fileName,
        id: '',
      }
      this.$http.post(url, {"formData": newformData}).then((response) => {
        if(response.data.success) {
          vm.isLoading = false;
          vm.$router.push('/user/profile');
        }
      })
      .catch(function(error){
        console.error("寫入錯誤",error);
        alert('發佈失敗，請稍後再重試，或聯繫迷聲客服中心');
        vm.isLoading = false;
      });
    },
    uploadSong() {
      const vm = this;
      const timestamp = Number(new Date()); // 檔名設定
      const fileName = timestamp.toString() + vm.userData.uid;
      const storageRef = firebase.storage().ref('music/' + fileName); //資料夾+黨名
      const file = document.getElementById('song').files[0];
      const uploadTask = storageRef.put(file);
      vm.formData.fileName = fileName;
      uploadTask.on('state_changed', function(snapshot){
        var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        $('#progressing').width(progress + "%");
        switch (snapshot.state) {
          case firebase.storage.TaskState.PAUSED:
            vm.tempDom.uploadText = '上傳終止'
            break;
          case firebase.storage.TaskState.RUNNING:
            vm.tempDom.uploadText = '正在上傳音檔，請勿離開視窗'
            break;
        }
      },
      function(error) {
        vm.tempDom.uploadText = '圖片上傳失敗，建議重新整理後重試'
      },
      function() {
        // 取得圖片位置
        uploadTask.snapshot.ref.getDownloadURL().then(function(downloadURL) {
          vm.formData.url = downloadURL;
          vm.tempDom.uploadText = '';
          vm.tempDom.uploadDone = true;
        });
      });
    },
  },
  created() {
    const vm = this;
    window.onbeforeunload = (e) => {
      e.returnValue = '離開後不會保存內容';
    };
  },
  beforeRouteLeave (to, from , next) {
    if(!this.tempDom.alldataDone) {
      const answer = window.confirm('確定要離開嗎？系統不會保存目前內容')
      if (answer) {
        next()
      } else {
        next(false)
      }
    } else {
      next()
    }
  }
}
</script>

<style lang="scss" scoped>
#upload-img{
  background-color: rgba(0,0,0,0.5);
}
textarea{
  resize: none;
  &::-webkit-scrollbar {
    width: 0px;
  }
  &::-webkit-scrollbar-track {
    background: transparent
  }
  &::-webkit-scrollbar-thumb {
    background-color: transparent;
  }
  &::-webkit-scrollbar-thumb:hover {
    background-color: transparent;
  }
}

.files{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 30px 0;
  cursor: pointer;
  input{
    border-radius: 5px;
    border: 1px solid #ccc;
    margin-top: 10px;
    padding: 5px;
    max-width: 100%;
  }
  .select-img{
    width: 225px;
  }
  .img-preview{
    height: 300px;
    width: 100%;
    overflow: hidden;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px dashed #ccc;
    border-radius: 5px;
    img{
      max-width: 600px;
      position: absolute;
    }
  }
}
</style>
